import { FC, HTMLAttributes } from 'react'
import {
	formatCurrency,
	formatDate,
	formatNumber,
	getQuarter,
} from 'utils/utils'
import styles from './CardItem.module.scss'

interface IProps extends HTMLAttributes<HTMLDivElement> {
	styles?: { [key: string]: string }
	value?: string | number | Date | boolean | null
	type?: string
	title?: string
	unitName?: string
	noMargin?: boolean
}

const CardItem: FC<IProps> = ({
	children,
	className,
	value,
	type,
	title,
	unitName,
	noMargin,
	...props
}) => {
	let itemValue = value

	switch (type) {
		case 'docDate':
			const date = new Date(+value * 1000)
			itemValue = `${formatDate(date, false)} (${getQuarter(date)})`
			break
		case 'quantity':
			itemValue = `${formatNumber(value as number)} ${unitName || ''}.`
			break
		case 'price':
			itemValue = `${formatCurrency(value as number)} .`
			break
		default:
			itemValue = value
			break
	}

	return (
		<div
			className={`${styles.item} ${className} ${noMargin && styles.noMargin}`}
			{...props}
		>
			<div className={styles.item__title}>{title}</div>
			<div className={styles.item__boty}>
				{value && value !== Infinity ? '' + itemValue : ' '}
			</div>
		</div>
	)
}

export default CardItem
