import Button from 'components/shared/Button'
import { Loader } from 'components/vsa'
import useStores from 'hooks/useStores'
import { observer } from 'mobx-react-lite'
import { formatDate } from 'utils/utils'
import CardItem from '../CardItem'
import styles from './LinkCard.module.scss'

export interface LinkCardProps {
	link?: go.Link
	cardColor?: string
	rbData?: any
	isLoading?: boolean
}

export default observer(function LinkCard({
	cardColor,
	link,
	rbData,
	isLoading,
}: LinkCardProps) {
	const { infoCardStore } = useStores()
	const { changeRbDataVisibility } = infoCardStore

	return (
		<div className={`${styles.section}`} style={{ borderColor: cardColor }}>
			{link.data.rbOperationId ? (
				<div className={styles.rb}>
					{isLoading && <Loader className="loader" />}
					<div className={styles.rb__row}>
						<div className={styles.rb__col}>
							<CardItem title="" value={rbData.tnved || '-'} />
						</div>
						<div className={styles.rb__col}>
							<CardItem
								title=""
								value={rbData.productName || '-'}
							/>
						</div>
					</div>
					<div className={styles.rb__row}>
						<div className={styles.rb__col}>
							{rbData.quantity && (
								<CardItem
									title=""
									value={rbData.quantity}
									type="quantity"
									unitName={rbData.unitName}
								/>
							)}
						</div>
						<div className={styles.rb__col}>
							{rbData.costRub && (
								<CardItem title="" value={rbData.costRub} type="price" />
							)}
						</div>
					</div>
					<div className={styles.rb__row}>
						<div className={styles.rb__col_full}>
							<CardItem title="" value={rbData.sellerId} />
							{(rbData.sellerName ||
								rbData.sellerLastName ||
								rbData.sellerMiddleName) && (
								<CardItem
									title=""
									value={`${rbData.sellerName || ''} ${
										rbData.sellerLastName || ''
									} ${rbData.sellerMiddleName || ''}`}
									noMargin={true}
								/>
							)}
							{rbData.sellerAddress && (
								<CardItem
									title=""
									value={rbData.sellerAddress}
									noMargin={true}
								/>
							)}
						</div>
					</div>
					<div className={styles.rb__row}>
						<div className={styles.rb__col_full}>
							<CardItem title="" value={rbData.buyerId} />
							{(rbData.buyerName ||
								rbData.buyerLastName ||
								rbData.buyerMiddleName) && (
								<CardItem
									title=""
									value={`${rbData.buyerName || ''} ${
										rbData.buyerLastName || ''
									} ${rbData.buyerMiddleName || ''}`}
									noMargin={true}
								/>
							)}
							{rbData.buyerAddress && (
								<CardItem
									title=""
									value={rbData.buyerAddress}
									noMargin={true}
								/>
							)}
						</div>
					</div>
					<div className={styles.rb__row}>
						<div className={styles.rb__col_full}>
							<CardItem
								title=" "
								value={rbData.docTypeName || '-'}
							/>
						</div>
					</div>
					<div className={styles.rb__row}>
						<div className={styles.rb__col}>
							<CardItem title=". " value={rbData.docId || '-'} />
						</div>
						<div className={styles.rb__col}>
							<CardItem
								title=""
								value={formatDate(rbData.docDate, false)}
							/>
						</div>
					</div>
					<div className={styles.rb__row}>
						<div className={styles.rb__col}>
							{rbData.invoiceSer && (
								<CardItem title="" value={rbData.invoiceSer || '-'} />
							)}
						</div>
						<div className={styles.rb__col}>
							{rbData.invoiceNum && (
								<CardItem title="" value={rbData.invoiceNum || '-'} />
							)}
						</div>
					</div>
					<div className={styles.rb__row}>
						<div className={`${styles.rb__col_full} ${styles.rb__col_button}`}>
							<Button color="second" onClick={changeRbDataVisibility}>
								       
							</Button>
						</div>
					</div>
				</div>
			) : (
				<>
					<CardItem
						title="   "
						value={
							link.data.isGroupLink
								? link.data.groupQuantity
								: link.data.quantity
						}
						type="quantity"
					/>
					<CardItem
						title="  "
						value={
							link.data.isGroupLink
								? link.data.groupFirstDate
								: link.data.first_doc_date
						}
						type="docDate"
					/>
					<CardItem
						title="   "
						value={
							link.data.isGroupLink
								? link.data.groupOperationsCount
								: link.data.operations_count
						}
					/>
					<CardItem
						title="  "
						value={
							link.data.isGroupLink
								? link.data.groupLastDate
								: link.data.last_doc_date
						}
						type="docDate"
					/>
					<CardItem
						title="  "
						value={
							link.data.isGroupLink
								? link.data.groupAveragePrice
								: link.data.average_price
						}
						type="price"
					/>
					<CardItem
						title="    "
						value={
							link.data.isGroupLink
								? link.data.groupTotalCost
								: link.data.total_cost
						}
						type="price"
					/>
				</>
			)}
		</div>
	)
})
