import useStores from 'hooks/useStores'
import iconMinusLight from 'icons/icon_minus.png'
import iconMinusDark from 'icons/icon_minus_dark.png'
import iconPlusLight from 'icons/icon_plus.png'
import iconPlusDark from 'icons/icon_plus_dark.png'
import { observer } from 'mobx-react-lite'
import { getTrackBackground, Range } from 'react-range'
import './Zoom.css'

const Zoom = observer(() => {
	const { appStore, zoomStore } = useStores()

	const iconPlus: any = appStore.theme === 'dark' ? iconPlusLight : iconPlusDark

	const iconMinus: any =
		appStore.theme === 'dark' ? iconMinusLight : iconMinusDark

	const values = [zoomStore.zoom]

	return (
		<div style={{ width: '100%', display: 'flex', gap: 8 }}>
			<div
				className="zoom__minus"
				onClick={() => zoomStore.setZoom(zoomStore.zoom - 5)}
			>
				<img src={iconMinus} alt=" " />
			</div>
			<Range
				min={zoomStore.minZoom}
				max={zoomStore.maxZoom}
				values={values}
				onChange={(values) => zoomStore.setZoom(values[0])}
				renderTrack={({ props, children }) => (
					<div
						className="input-range__track-container"
						onMouseDown={props.onMouseDown}
						onTouchStart={props.onTouchStart}
						style={props.style}
					>
						<div
							ref={props.ref}
							className="input-range__track"
							style={{
								background: getTrackBackground({
									values,
									min: zoomStore.minZoom,
									max: zoomStore.maxZoom,
									colors: ['#859de0', '#5a6487'],
								}),
							}}
						>
							{children}
						</div>
					</div>
				)}
				renderThumb={({ props }) => (
					<div className="input-range__thumb" {...props} />
				)}
			/>
			<div
				className="zoom__plus"
				onClick={() => zoomStore.setZoom(zoomStore.zoom + 5)}
			>
				<img src={iconPlus} alt=" " />
			</div>
		</div>
	)
})

export default Zoom
