import nodeTypes from 'constants/nodeTypes'
import useStores from 'hooks/useStores'
import iconClose from 'icons/icon_close.png'
import { observer } from 'mobx-react-lite'
import { Scrollbars } from 'react-custom-scrollbars-2'
import UnifiedClientProxy from 'utils/UnifiedClientProxy'
import { formatCurrency, formatNumber, getQuarter } from 'utils/utils'
import LinkCard from './components/LinkCard'
import './InfoCard.css'

export default observer(function InfoCard() {
	const { infoCardStore, appStore, graphStore } = useStores()

	const { selectedNode, selectedLink, cardColor, rbCardData, isRbCardLoading } =
		infoCardStore

	const {
		modes: {
			card: { card: catCard },
		},
		graphData: { graphInfo },
	} = graphStore

	const { theme } = appStore

	const open: string = infoCardStore.open ? 'open' : ''
	const collapsed: string = infoCardStore.collapsed ? 'collapsed' : ''

	const node: go.Node = selectedNode
	const link: go.Link = selectedLink

	const cardDataItem = (item: string): string | number => {
		if (node && node.data.node[item]) {
			return node.data.node[item]
		} else {
			return null
		}
	}

	const GenCardItem = observer(
		({
			value,
			title,
			unit,
		}: {
			value: string | number
			title: string
			unit?: string
		}) => {
			if (node && value) {
				return (
					<div className="info-card__item">
						<div className="info-card__item-title">{title}</div>
						<div className="info-card__item-body">
							{value} {unit ? unit : null}
						</div>
					</div>
				)
			}
			return null
		},
	)

	const CardItem = observer(
		({ value, title }: { value: string; title: string }) => {
			if (node && (node.data.node[value] || node.data.node[value] === 0)) {
				if (value === 'root' || value === 'leaf') {
					value = node.data.node[value] ? '' : ''
				} else if (value === 'TYPE') {
					value = nodeTypes[node.data.node[value]]
				} else if (value === 'balance') {
					const date = new Date(+node.data.node.last_doc_date * 1000)
					const quarter = node.data.node.last_doc_date
						? ` (  ${getQuarter(date, true)})`
						: ''
					value = `${formatNumber(node.data.node[value])} ${
						graphInfo.unit_name || ''
					}.${quarter}`
				} else if (
					value === 'balance_br' ||
					value === 'credit' ||
					value === 'debet'
				) {
					value = `${formatNumber(node.data.node[value])} ${
						graphInfo.unit_name || ''
					}.`
				} else if (value === 'cost_in') {
					value = formatCurrency(node.data.node[value]) + ' .'
				} else {
					value = node.data.node[value]
				}

				return (
					<div className="info-card__item">
						<div className="info-card__item-title">{title}</div>
						<div className="info-card__item-body">{value}</div>
					</div>
				)
			} else {
				return null
			}
		},
	)

	const CardHeader = observer(() => {
		if (
			node &&
			(type === 'Kazakhstan' ||
				type === 'Armenia' ||
				type === 'Belarus' ||
				type === 'Kyrgyzstan' ||
				type === 'Russia')
		) {
			return (
				<div>
					<div className="info-card__header">
						<div className="info-card__subtitle"></div>
						<div className="info-card__title">{cardDataItem('short_name')}</div>
					</div>
					<div className="info-card__info">
						<div className="info-card__info-item">
							<span>:</span> {cardDataItem('inn')}
						</div>
						<div className="info-card__info-item">
							<span>:</span> {cardDataItem('kpp')}
						</div>
					</div>
				</div>
			)
		}
		if (node && type === 'Declaration') {
			return (
				<>
					<div className="info-card__header">
						<div className="info-card__subtitle">
							  
						</div>
						<div className="info-card__title">{graphInfo.orig_co_name}</div>
					</div>
					<div className="info-card__header">
						<div className="info-card__subtitle"> </div>
						<div className="info-card__title">
							{cardDataItem('co_code_name')}
						</div>
					</div>
				</>
			)
		}
		return null
	})

	const type = node ? node.data.node.business_role : null

	const Tax = observer(() => {
		if (node && type === 'Russia') {
			return <CardItem title=" " value="tax_type_name" />
		}
		return null
	})

	const Balance = observer(() => {
		if (node && type === 'Russia') {
			return (
				<CardItem
					title=" ,    "
					value="balance"
				/>
			)
		}
		return null
	})

	const BalanceBr = observer(() => {
		if (node && type === 'Russia') {
			return (
				<CardItem
					title=" ,    "
					value="balance_br"
				/>
			)
		}
		return null
	})

	const Rntp = observer(() => {
		if (
			node &&
			(node.data.node.root ||
				type === 'Out_to_EAEU' ||
				type === 'Disposal' ||
				type === 'Force_Majeure' ||
				type === 'Sale_In_Retail' ||
				type === 'Export' ||
				type === 'To_Capital' ||
				type === 'Bankruptcy' ||
				type === 'Confiscat_by_Goverment' ||
				type === 'To_Fixed_Assets' ||
				type === 'Balance_in_RF' ||
				type === 'Out_Fixed_Assets' ||
				type === 'Out_Capital' ||
				type === 'Return_From_Retail' ||
				type === 'Return_Lost_Goods' ||
				type === 'Confiscat' ||
				type === 'From_EAEU' ||
				type === 'Production' ||
				type === 'Declaration' ||
				type === 'Sale_Out_Retail' ||
				type === 'In_Retail_Free' ||
				type === 'Retail_diplomatic' ||
				type === 'In_Lost_Goods')
		) {
			return <GenCardItem title="" value={graphInfo.ipt} />
		}
		return null
	})

	const Credit = observer(() => {
		if (
			node &&
			(type === 'Declaration' ||
				type === 'From_EAEU' ||
				type === 'Balance_in_RF' ||
				type === 'Return_Lost_Goods' ||
				type === 'Sale_Out_Retail' ||
				type === 'Confiscat')
		) {
			return (
				<CardItem
					title=" ,   "
					value="credit"
				/>
			)
		}
		return null
	})

	const Debet = observer(() => {
		if (
			node &&
			(type === 'Out_to_EAEU' ||
				type === 'Disposal' ||
				type === 'Production' ||
				type === 'Force_Majeure' ||
				type === 'Sale_In_Retail' ||
				type === 'Export' ||
				type === 'In_Retail_Free' ||
				type === 'Confiscat_by_Goverment' ||
				type === 'Retail_diplomatic' ||
				type === 'In_Lost_Goods')
		) {
			return (
				<CardItem
					title=" ,   "
					value="debet"
				/>
			)
		}
		return null
	})

	const CatCardItem = observer(
		({ value, title }: { value: string; title: string }) => {
			if (catCard && (catCard[value] || catCard[value] === 0)) {
				if (value === 'quantity') {
					value =
						formatNumber(catCard[value]) +
						' ' +
						(graphInfo.unit_name || '') +
						'.'
				} else if (
					value === 'cost' ||
					value === 'price' ||
					value === 'tax_amount'
				) {
					value = formatCurrency(catCard[value]) + ' .'
				} else {
					value = catCard[value]
				}

				return (
					<div className="info-card__item">
						<div className="info-card__item-title">{title}</div>
						<div className="info-card__item-body">{value}</div>
					</div>
				)
			} else {
				return null
			}
		},
	)

	const CatGoButton = observer(({ type }: { type: string }) => {
		if (catCard && type === 'card') {
			return (
				<div
					className="button info-card__button"
					onClick={() => {
						return UnifiedClientProxy.openPcCard({
							key: catCard.card_key,
						})
					}}
				>
					 
				</div>
			)
		} else if (catCard && type === 'profile') {
			return (
				<div
					className="button info-card__button"
					onClick={() => {
						return UnifiedClientProxy.openPcProfile({
							key: catCard.profile_key,
						})
					}}
				>
					 
				</div>
			)
		}

		return null
	})

	const GoToUpd = observer(() => {
		if (node && node.data.node.category !== 'Abstract') {
			return (
				<div
					className="button info-card__button"
					onClick={() => {
						return UnifiedClientProxy.openReceivedDocumentsRegistry({
							ipt: graphInfo.ipt,
							inn: node.data.node.inn,
						})
					}}
				>
					  /
				</div>
			)
		}

		return null
	})

	return (
		<div className={`info-card ${theme} ${open} ${collapsed}`}>
			<Scrollbars className="info-card__body" autoHide>
				<div
					className="info-card__close"
					onClick={() => graphStore.modes.card.hideCard()}
				>
					<img src={iconClose} alt="" />
				</div>

				{link && link.data ? (
					<LinkCard
						link={link}
						cardColor={cardColor}
						rbData={rbCardData}
						isLoading={isRbCardLoading}
					/>
				) : (
					<>
						<CardHeader />
						<div
							className="info-card__section"
							style={{ borderColor: cardColor }}
						>
							<Tax />
							<Balance />
							<BalanceBr />
							<Rntp />
							<Debet />
							<Credit />
							<CatCardItem title=" " value="quantity" />
							<CatCardItem title=" " value="cost" />
							<CatCardItem title="   " value="price" />
							<CatCardItem
								title="   "
								value="tax_amount"
							/>
							<CatGoButton type="card" />
							<CatGoButton type="profile" />
							<GoToUpd />
						</div>
					</>
				)}
			</Scrollbars>
		</div>
	)
})
