import GraphDetailModal from 'components/GraphDetailModal/GraphDetailModal'
import { ReactDiagram } from 'gojs-react'
import useStores from 'hooks/useStores'
import useTheme from 'hooks/useTheme'
import { observer } from 'mobx-react-lite'
import SideMenu from '../SideMenu'
import styles from './Diagram.module.scss'

// go.Diagram.licenseKey = '73f947e1b06231b700ca0d2b113f69ed1bb37b369e8c1ea05e0241f1eb01691c2a99fa6857d4dbc0d2fc46fe4e78c2d0ddc6612a924d573fe264d4db51f280ace23173e04409559ea70225c286f235a2'

const Diagram = observer(() => {
	const { appStore, graphStore } = useStores()

	useTheme(graphStore)

	return (
		<div className={styles.diagramContainer}>
			{graphStore.graphData && <SideMenu />}
			<GraphDetailModal />
			<ReactDiagram
				divClassName="diagram-component"
				initDiagram={() => graphStore.diagram}
				nodeDataArray={graphStore.graphData?.graphNodes || []}
				linkDataArray={graphStore.graphData?.graphEdges || []}
			/>
			<div id="tooltip" className={`${styles.tooltip} ${appStore.theme}`}></div>
		</div>
	)
})

export default Diagram
