import useStores from 'hooks/useStores'
import iconClose from 'icons/icon_close.png'
import { observer } from 'mobx-react-lite'
import { Resizable } from 're-resizable'
import { useRef } from 'react'
import { Scrollbars } from 'react-custom-scrollbars-2'
import Draggable from 'react-draggable'
import UnifiedClientProxy from 'utils/UnifiedClientProxy'
import { formatCurrency, formatDate, formatNumber } from 'utils/utils'
import './RiskCard.css'

export default observer(function RiskCard() {
	const cardRef = useRef<HTMLDivElement>()
	const { appStore, riskCardStore, graphStore } = useStores()

	riskCardStore.card = cardRef.current

	const { setCardDir } = riskCardStore

	const graphInfo = graphStore.graphData.graphInfo

	/*const RiskRntp = () => {
      return (
        <div className="risk__item">
          <div className="risk__item-title">
            {riskCardStore.connectedRntp.length ? ' :' : ''}
          </div>
          { riskRntpItems }
        </div>
      )
    }

    const riskRntpItems = riskCardStore.connectedRntp.map((rntp, i) => {
      return(
        <div key = { `rntp_${i}` }  className="risk__item-item">{ rntp }</div>
      )
    })*/

	const RiskItem = observer(
		({
			value,
			title,
			unit,
		}: {
			value: string | number
			title: string
			unit?: string
		}) => {
			return value || value === 0 || value === '0' ? (
				<div className="risk__item">
					<div className="risk__item-title">{title}</div>
					<div className="risk__item-item">
						{value} {unit ? unit : null}
					</div>
				</div>
			) : null
		},
	)

	const resortRntpItems = riskCardStore.resortRntp.map((rntp, i) => {
		return (
			<div
				onClick={() => {
					UnifiedClientProxy.openGraph({
						ipt: rntp.IPT,
						inn: riskCardStore.currentRiskNode.data.inn,
						theme: appStore.theme,
					})
					//console.log(riskCardStore.currentRiskNode.data.inn, rntp.IPT, appStore.theme)
				}}
				key={`rntp_${i}`}
				className="risk__item-item risk__resort-rntp rntp"
			>
				<div className="rntp_item">
					<div className="rntp_title"></div>
					<div className="rntp_body">{rntp.IPT}</div>
				</div>
				<div className="rntp_item">
					<div className="rntp_title"></div>
					<div className="rntp_body">
						{formatNumber(rntp.Balance) +
							' ' +
							(graphInfo.unit_name || '') +
							'.'}
					</div>
				</div>
			</div>
		)
	})

	const ResortRntp = observer(() => {
		return (
			<div className="risk__item">
				<div className="risk__item-title">
					{riskCardStore.resortRntp.length ? ' :' : ''}
				</div>
				{resortRntpItems}
			</div>
		)
	})

	const code = riskCardStore.currentRisk ? riskCardStore.currentRisk.Code : 0

	const InValue = observer(() => {
		if (code === 201 || code === 203 || code === 205) {
			return (
				<RiskItem
					title=" "
					value={formatNumber(riskCardStore.currentRiskNode.data.node.debet)}
					unit={(graphInfo.unit_name || '') + '.'}
				/>
			)
		}
		return null
	})

	const OutValue = observer(() => {
		if (code === 201 || code === 203 || code === 205) {
			return (
				<RiskItem
					title=" "
					value={formatNumber(riskCardStore.currentRiskNode.data.node.credit)}
					unit={(graphInfo.unit_name || '') + '.'}
				/>
			)
		}
		return null
	})

	const Balance = observer(() => {
		if (code === 201 || code === 203 || code === 205 || code === 206) {
			return (
				<RiskItem
					title="  "
					value={formatNumber(riskCardStore.currentRiskNode.data.node.balance)}
					unit={(graphInfo.unit_name || '') + '.'}
				/>
			)
		}
		return null
	})

	const MixBalance = observer(() => {
		if (code === 206) {
			return (
				<RiskItem
					title="  "
					value={formatNumber(riskCardStore.currentRisk.MixBalance)}
					unit={(graphInfo.unit_name || '') + '.'}
				/>
			)
		}
		return null
	})

	const ImportBalance = observer(() => {
		if (code === 211 || code === 212) {
			return (
				<RiskItem
					title=" "
					value={formatNumber(riskCardStore.currentRisk.Balance)}
					unit={(graphInfo.unit_name || '') + '.'}
				/>
			)
		}
		return null
	})

	const CoeffDeviation = observer(() => {
		if (code === 204) {
			return (
				<RiskItem
					title="  , %"
					value={formatNumber(riskCardStore.currentRisk.coeffDeviation)}
					unit="%"
				/>
			)
		}
		return null
	})

	const PriceIn = observer(() => {
		if (code === 204 || code === 207) {
			return (
				<RiskItem
					title="  "
					value={formatCurrency(riskCardStore.currentRisk.priceIn)}
					unit="."
				/>
			)
		}
		return null
	})

	const PriceOut = observer(() => {
		if (code === 204 || code === 207) {
			return (
				<RiskItem
					title="  "
					value={formatCurrency(riskCardStore.currentRisk.priceOut)}
					unit="."
				/>
			)
		}
		return null
	})

	const LastDocDate = observer(() => {
		if (code === 204 || code === 207) {
			// console.log(riskCardStore.currentRiskNode.data)
			const date = new Date(
				riskCardStore.currentRiskNode.data.node.last_doc_date * 1000,
			)
			return (
				<RiskItem
					title="  "
					value={formatDate(date, false)}
				/>
			)
		}
		return null
	})

	const ShowPotentialButton = observer(({ text }: { text: string }) => {
		if (code !== 201) return null
		return (
			<div
				className="button risk__button"
				onClick={() => {
					const potentialMode = graphStore.modes.potential
					if (potentialMode.isEnabled) {
						return graphStore.modes.potential.disable()
					}
					graphStore.modes.potential.enable(
						riskCardStore.currentRiskNode,
						riskCardStore.currentRisk,
					)
				}}
			>
				{text}
			</div>
		)
	})

	let riskIcon

	if (code === 201 || code === 203) {
		riskIcon = riskCardStore.currentRiskNode.data.balanceIcon
	} else if (code === 204) {
		riskIcon = riskCardStore.currentRiskNode.data.speedRiskIcon
	} else if (code === 205) {
		riskIcon = riskCardStore.currentRiskNode.data.zatovRiskIcon
	} else if (code === 206) {
		riskIcon = riskCardStore.currentRiskNode.data.resortRiskIcon
	} else if (code === 207) {
		riskIcon = riskCardStore.currentRiskNode.data.speedRiskIcon
	} else if (code === 211) {
		riskIcon = riskCardStore.currentRiskNode.data.lowImportRiskIcon
	} else if (code === 212) {
		riskIcon = riskCardStore.currentRiskNode.data.highImportRiskIcon
	} else if (code === 500) {
		riskIcon = riskCardStore.currentRiskNode.data.fakeRiskIcon
	}

	return (
		<Draggable nodeRef={cardRef} handle=".risk__drag">
			<div
				ref={cardRef}
				className={`risk ${riskCardStore.riskCardIsOpen ? 'open' : 'close'} ${
					appStore.theme
				}`}
			>
				<Resizable
					className="risk-resizable"
					defaultSize={{ width: 340, height: 280 }}
					minWidth={340}
					minHeight={280}
					onResizeStart={(e, dir) => setCardDir(dir)}
				>
					<div
						className="risk__close"
						onClick={() => riskCardStore.closeRiskCard()}
					>
						<img src={iconClose} alt="" />
					</div>
					<div className="risk__drag">
						<div className="risk__header">
							<div className="risk__image">
								<img src={riskIcon} alt="" />
							</div>
							<div className="risk__info">
								<div className="risk__title">
									{riskCardStore.currentRisk
										? riskCardStore.currentRisk.Name
										: ''}
								</div>
								<div className="risk__nodetitle">
									{riskCardStore.currentRiskNode
										? riskCardStore.currentRiskNode.data.node.short_name
										: ''}
								</div>
							</div>
						</div>

						<div className="risk__inn">
							<span>:</span>
							{riskCardStore.currentRiskNode
								? riskCardStore.currentRiskNode.data.node.inn
								: ''}
						</div>

						<Scrollbars autoHide style={{ height: '100%', width: '100%' }}>
							<Balance />
							<InValue />
							<OutValue />
							{/* <RiskRntp/> */}
							<MixBalance />
							<ImportBalance />
							<ResortRntp />
							<CoeffDeviation />
							<PriceIn />
							<PriceOut />
							<LastDocDate />
							<ShowPotentialButton
								text={`${
									graphStore.modes.potential.isEnabled &&
									riskCardStore.currentRisk &&
									riskCardStore.currentRisk.RiskId ===
										graphStore.modes.potential.currentPotentialRisk?.RiskId
										? ''
										: ''
								}  `}
							/>
						</Scrollbars>
					</div>
				</Resizable>
			</div>
		</Draggable>
	)
})
