import Overview from 'components/Overview'
import useStores from 'hooks/useStores'
import { observer } from 'mobx-react-lite'
import GraphAppStore from 'stores/GraphAppStore'
import GraphInfo from '../GraphInfo'
import InfoCard from '../InfoCard'
import RbCard from '../InfoCard/components/RbCard'
import Legend from '../Legend'
import Loader from '../Loader'
import Menu from '../Menu'
import PotentialCard from '../PotentialCard'
import RiskCard from '../RiskCard'
import ZvCard from '../ZvCard'
import Diagram from './Diagram'
import styles from './Graph.module.scss'
import GraphProvider from './GraphProvider'

interface GraphProps {
	appStore: GraphAppStore
}

export default observer(function GraphApp({ appStore }: GraphProps) {
	return (
		<GraphProvider appStore={appStore}>
			<Graph />
		</GraphProvider>
	)
})

const Graph = observer(() => {
	const { appStore, graphStore, infoCardStore } = useStores()

	return (
		<div className={`${styles.graph} ${appStore.theme || ''}`}>
			<Menu />
			<Diagram />
			{graphStore.graphData && (
				<>
					<Loader />
					<Legend />
					<Overview />
					<GraphInfo />
					<InfoCard />
					<RiskCard />
					<ZvCard />
					<PotentialCard />
					{infoCardStore.rbDataIsOpen && <RbCard />}
				</>
			)}
		</div>
	)
})
