import useStores from 'hooks/useStores'
import iconClose from 'icons/icon_close.png'
import { observer } from 'mobx-react-lite'
import { useRef } from 'react'
import Draggable from 'react-draggable'
import './ZvCard.css'

export default observer(function ZvCard() {
	const { appStore, graphStore } = useStores()
	const nodeRef = useRef<HTMLDivElement>()

	const ZvItem = observer(({ zv }: { zv: any }) => {
		const activeClass =
			graphStore.modes.zv.currentZv &&
			graphStore.modes.zv.currentZv.statement_name === zv.statement_name
				? 'active'
				: ''
		return (
			<div
				className={`potential-card__item ${activeClass}`}
				onClick={() => {
					graphStore.modes.zv.changeZvVisible(zv)
				}}
			>
				{zv.statement_name}
			</div>
		)
	})

	const zvs: any[] = graphStore.graphData.graphInfo.statement_info || []

	const zvItems = zvs.map((zv, i) => {
		return <ZvItem zv={zv} key={i} />
	})

	const activeClass = graphStore.modes.zv.currentZv ? 'active' : ''

	return (
		<Draggable nodeRef={nodeRef}>
			<div
				ref={nodeRef}
				className={`potential-card ${
					graphStore.modes.zv.isEnabled ? 'open' : 'close'
				} ${appStore.theme} ${activeClass}`}
			>
				<div
					className="potential-card__close"
					onClick={() => graphStore.modes.zv.toggle()}
				>
					<img src={iconClose} alt="" />
				</div>
				<div className="potential-card__title"> </div>
				<div className="potential-card__items">{zvItems}</div>
			</div>
		</Draggable>
	)
})
