import useStores from 'hooks/useStores'
import iconClose from 'icons/icon_close.png'
import { observer } from 'mobx-react-lite'
import { Scrollbars } from 'react-custom-scrollbars-2'
import { formatCurrency, formatNumber } from 'utils/utils'
import './GraphInfo.css'

export default observer(function GraphInfo() {
	const { appStore, graphStore, graphInfoStore } = useStores()

	const graphInfo: any = graphStore.graphData.graphInfo
	const open: string = graphInfoStore.open ? 'open' : ''

	const GenCardItem = observer(
		({
			value,
			title,
			unit,
		}: {
			value: string | number
			title: string
			unit?: string
		}) => {
			if (value) {
				return (
					<div className="info-card__item">
						<div className="info-card__item-title">{title}</div>
						<div className="info-card__item-body">
							{value} {unit ? unit : null}
						</div>
					</div>
				)
			} else {
				return null
			}
		},
	)

	const Tnved = () => {
		if (graphInfo.tnved) {
			return (
				<div className="info-card__item">
					<div className="info-card__item-title">   </div>
					<div
						className={`info-card__item-body ${
							graphInfo.tnved_description ? 'red' : ''
						}`}
					>
						{graphInfo.tnved}
						{graphInfo.tnved_description && (
							<p>{graphInfo.tnved_description}</p>
						)}
					</div>
				</div>
			)
		}
		return null
	}

	/*const OkpdList = ({ value }: { value: string }) => {
      if(value){
        return(
          <div className="info-card__item">
            <div className="info-card__item-body">{ value } </div>
          </div>
        );
      }else{
        return null;
      }
    }*/

	return (
		<div className={`info-card ${appStore.theme} ${open}`}>
			<Scrollbars autoHide style={{ height: '100%' }}>
				<div className="info-card__close" onClick={graphInfoStore.setClose}>
					<img src={iconClose} alt="" />
				</div>
				<div className="info-card__header">
					<div className="info-card__subtitle"> </div>
					<div className="info-card__title">{graphInfo.p_num}</div>
				</div>
				<div className="info-card__info">
					<div className="info-card__info-item">
						<span>:</span> {graphInfo.ipt}
					</div>
				</div>
				<div className="info-card__section">
					<Tnved />
					<GenCardItem title="  2" value={graphInfo.Okpd2_list} />
					<GenCardItem
						title=" ,   "
						value={formatNumber(graphInfo.imp_cnt)}
						unit={(graphInfo.unit_name || '') + '.'}
					/>
					<GenCardItem
						title=" ,   "
						value={formatNumber(graphInfo.out_cnt)}
						unit={(graphInfo.unit_name || '') + '.'}
					/>
					<GenCardItem
						title=" ,   "
						value={formatNumber(graphInfo.in_cnt)}
						unit={(graphInfo.unit_name || '') + '.'}
					/>
					<GenCardItem
						title="    "
						value={formatNumber(graphInfo.balance)}
						unit={(graphInfo.unit_name || '') + '.'}
					/>
					<GenCardItem
						title="  "
						value={formatCurrency(graphInfo.price_min)}
						unit="."
					/>
					<GenCardItem
						title="  "
						value={formatCurrency(graphInfo.price_max)}
						unit="."
					/>
				</div>
			</Scrollbars>
		</div>
	)
})
