import { ReportingModal } from 'components/ReportingModal/ReportingModal'
import { StatusRequest } from 'constants/statusRequest'
import useStores from 'hooks/useStores'
import { runInAction } from 'mobx'
import { observer } from 'mobx-react-lite'
import { Scrollbars } from 'react-custom-scrollbars-2'
import { menuIconsDark, menuIconsLight } from 'utils/icons/menuIcons'
import {
	sideMenuIconsDark,
	sideMenuIconsLight,
} from 'utils/icons/sideMenuIcons'
import { formatterDateFromString } from 'utils/utils'
import './SideMenu.css'

export default observer(function SideMenu() {
	const { appStore, graphStore, graphInfoStore, sideMenuStore, legendStore } =
		useStores()

	const {
		lastRequestDate,
		status,
		lastEaeuRequestDate,
		eaeuStatus,
		graphInfo,
	} = graphStore.graphData

	const {
		showTooltip,
		hideTooltip,
		isRequestLoading,
		fetchRequest,
		changeModalIsOpen,
		modalIsOpen,
		successModal,
		openModal,
		isEaeuRequestLoading,
		fetchEaeuRequest,
		changeEaeuModalIsOpen,
		eaeuModalIsOpen,
		successEaeuModal,
		openEaeuModal,
	} = sideMenuStore

	const sideMenuIcons: any =
		appStore.theme === 'dark' ? sideMenuIconsLight : sideMenuIconsDark
	const menuIcons: any =
		appStore.theme === 'dark' ? menuIconsLight : menuIconsDark

	const withoutZv =
		!graphInfo.statement_info ||
		!graphInfo.root_idx ||
		graphInfo.root_idx === '12' ||
		graphInfo.root_idx === 12

	const requestLoading = async () => {
		if (!isRequestLoading) {
			return status === StatusRequest.ISSHOWCLARIFICATION
				? openModal()
				: fetchRequest(graphInfo?.ipt)
		}

		return false
	}

	const renderModal =
		graphInfo?.ipt && lastRequestDate ? (
			<ReportingModal
				modalIsOpen={modalIsOpen}
				changeModalIsOpen={changeModalIsOpen}
				successModal={() => successModal(graphInfo.ipt)}
				pt={graphInfo.ipt}
				dateRequest={formatterDateFromString(lastRequestDate)}
			/>
		) : null

	const eaeuRequestLoading = async () => {
		if (!isEaeuRequestLoading) {
			return eaeuStatus === StatusRequest.ISSHOWCLARIFICATION
				? openEaeuModal()
				: fetchEaeuRequest(graphInfo?.ipt)
		}

		return false
	}

	const eaeuModal =
		graphInfo?.ipt && lastEaeuRequestDate ? (
			<ReportingModal
				modalIsOpen={eaeuModalIsOpen}
				changeModalIsOpen={changeEaeuModalIsOpen}
				successModal={() => successEaeuModal(graphInfo.ipt)}
				pt={graphInfo.ipt}
				dateRequest={formatterDateFromString(lastEaeuRequestDate)}
			/>
		) : null

	const rbRequestHeight = status && status !== StatusRequest.NONE ? 46 : 0
	const eaeuRequestHeight =
		eaeuStatus && eaeuStatus !== StatusRequest.NONE ? 46 : 0

	return (
		<>
			<div
				style={{
					maxHeight: 568 + rbRequestHeight + eaeuRequestHeight + 'px',
				}}
				onMouseLeave={sideMenuStore.setClose}
				className={`side-menu ${appStore.theme} ${
					sideMenuStore.open ? 'open' : ''
				}`}
			>
				<Scrollbars
					autoHide
					className="side-menu__scrollbar"
					renderTrackHorizontal={() => (
						<span className="side-menu__scrollbar-track" />
					)}
				>
					<div
						onMouseEnter={(e) => showTooltip(e, ' ')}
						onMouseLeave={hideTooltip}
						className={`side-menu__item ${graphInfoStore.open ? 'active' : ''}`}
						onClick={graphInfoStore.changeInfoVisible}
					>
						<span className="side-menu__image">
							<img src={sideMenuIcons.icon10} alt="" />
						</span>
						<span className="side-menu__title"> </span>
					</div>

					<div
						onMouseEnter={(e) =>
							showTooltip(e, '\\  ')
						}
						onMouseLeave={hideTooltip}
						className="side-menu__item"
					>
						<span data-tooltip="" className="side-menu__image">
							<img src={sideMenuIcons.icon1} alt="" />
						</span>
						<span className="side-menu__title">
							\  
						</span>
					</div>

					<div
						onMouseEnter={(e) =>
							showTooltip(e, '\\   ')
						}
						onMouseLeave={hideTooltip}
						className={`side-menu__item ${
							graphStore.valueVisible ? 'active' : ''
						}`}
						onClick={() => graphStore.changeValueVisible()}
					>
						<span data-tooltip="" className="side-menu__image">
							<img src={sideMenuIcons.icon2} alt="" />
						</span>
						<span className="side-menu__title">
							\   
						</span>
					</div>

					<div
						onMouseEnter={(e) =>
							showTooltip(e, '\\   ')
						}
						onMouseLeave={hideTooltip}
						className={`side-menu__item ${
							graphStore.labelVisible ? 'active' : ''
						}`}
						onClick={() => graphStore.changeLabelVisible()}
					>
						<span data-tooltip="" className="side-menu__image">
							<img src={sideMenuIcons.icon3} alt="" />
						</span>
						<span className="side-menu__title">
							\   
						</span>
					</div>

					<div
						onMouseEnter={(e) =>
							showTooltip(e, '\\  ')
						}
						onMouseLeave={hideTooltip}
						className={`side-menu__item ${
							graphStore.modes.potential.isEnabled ? 'active' : ''
						}`}
						onClick={() => {
							runInAction(() => {
								graphStore.modes.potential.showCard = true
							})
						}}
					>
						<span data-tooltip="" className="side-menu__image">
							<img src={sideMenuIcons.icon5} alt="" />
						</span>
						<span className="side-menu__title">
							\  
						</span>
					</div>

					<div
						onMouseEnter={(e) =>
							showTooltip(e, ' \\    ')
						}
						onMouseLeave={hideTooltip}
						className={`side-menu__item ${
							graphStore.modes.collapse.isEnabled ? 'active' : ''
						}`}
						onClick={() => {
							graphStore.modes.collapse.toggle()
						}}
					>
						<span data-tooltip="" className="side-menu__image">
							<img src={sideMenuIcons.icon6} alt="" />
						</span>
						<span className="side-menu__title">
							\ 
						</span>
					</div>

					<div
						onMouseEnter={(e) =>
							showTooltip(e, '\\  ')
						}
						onMouseLeave={hideTooltip}
						className={`side-menu__item ${
							graphStore.modes.zv.isEnabled ? 'active' : ''
						} ${withoutZv ? 'disabled' : ''}`}
						onClick={() => {
							return withoutZv ? null : graphStore.modes.zv.toggle()
						}}
					>
						<span data-tooltip="" className="side-menu__image">
							<img src={sideMenuIcons.icon7} alt="" />
						</span>
						<span className="side-menu__title">
							\  
						</span>
					</div>

					<div
						onMouseEnter={(e) =>
							showTooltip(e, '\\  ')
						}
						onMouseLeave={hideTooltip}
						className={`side-menu__item ${
							graphStore.modes.card.isEnabled ? 'active' : ''
						}`}
						onClick={() => graphStore.modes.card.toggleCardMode()}
					>
						<span data-tooltip="" className="side-menu__image">
							<img src={sideMenuIcons.icon11} alt="" />
						</span>
						<span className="side-menu__title">
							\  
						</span>
					</div>

					<div
						onMouseEnter={(e) => showTooltip(e, ' ')}
						onMouseLeave={hideTooltip}
						className={`side-menu__item ${
							graphStore.isHorizontal ? 'active' : ''
						}`}
						onClick={() => graphStore.setDirection(0)}
					>
						<span data-tooltip="" className="side-menu__image">
							<img src={menuIcons.iconMenu3} alt="" />
						</span>
						<span className="side-menu__title"> </span>
					</div>

					<div
						onMouseEnter={(e) => showTooltip(e, ' ')}
						onMouseLeave={hideTooltip}
						className={`side-menu__item ${
							graphStore.isHorizontal ? '' : 'active'
						}`}
						onClick={() => graphStore.setDirection(90)}
					>
						<span data-tooltip="" className="side-menu__image">
							<img src={menuIcons.iconMenu4} alt="" />
						</span>
						<span className="side-menu__title"> </span>
					</div>

					<div
						onMouseEnter={(e) => showTooltip(e, '  Excel')}
						onMouseLeave={hideTooltip}
						className={`side-menu__item active`}
						onClick={() => graphStore.exportToExcel()}
					>
						<span data-tooltip="" className="side-menu__image">
							<img src={sideMenuIcons.icon9} alt="" />
						</span>
						<span className="side-menu__title">  Excel</span>
					</div>

					<div
						onMouseEnter={(e) => showTooltip(e, '')}
						onMouseLeave={hideTooltip}
						className={`side-menu__item ${legendStore.open ? 'active' : ''}`}
						onClick={legendStore.changeLegendVisible}
					>
						<span data-tooltip="" className="side-menu__image">
							<img src={menuIcons.iconMenu2} alt="" />
						</span>
						<span className="side-menu__title"></span>
					</div>

					{status && status !== StatusRequest.NONE && (
						<div
							className={`side-menu__item active`}
							onClick={() => requestLoading()}
							onMouseEnter={(e) =>
								showTooltip(e, '       ')
							}
							onMouseLeave={hideTooltip}
						>
							<span data-tooltip="" className="side-menu__image">
								<img
									src={sideMenuIcons[isRequestLoading ? 'icon13' : 'icon12']}
									alt=""
								/>
							</span>
							<span className="side-menu__title">
								       
							</span>
						</div>
					)}

					{eaeuStatus && eaeuStatus !== StatusRequest.NONE && (
						<div
							className={`side-menu__item active`}
							onClick={() => eaeuRequestLoading()}
							onMouseEnter={(e) =>
								showTooltip(
									e,
									'         ',
								)
							}
							onMouseLeave={hideTooltip}
						>
							<span data-tooltip="" className="side-menu__image">
								<img
									src={
										sideMenuIcons[isEaeuRequestLoading ? 'icon13' : 'icon12']
									}
									alt=""
								/>
							</span>
							<span className="side-menu__title">
								         
							</span>
						</div>
					)}
				</Scrollbars>
			</div>
			<div
				id="side-menu__tooltip"
				className={`side-menu__tooltip ${appStore.theme}`}
			></div>
			{renderModal}
			{eaeuModal}
		</>
	)
})
