import * as go from 'gojs'
import { ReactOverview } from 'gojs-react'
import useStores from 'hooks/useStores'
import iconMap from 'icons/icon_map.png'
import { observer } from 'mobx-react-lite'
import { useState } from 'react'
import './Overview.css'
import Zoom from './Zoom'
import './Zoom/Zoom.css'

const Overview = observer(() => {
	const { appStore, graphStore, zoomStore } = useStores()
	const [mapIsOpen, setMapIsOpen] = useState(false)

	return (
		zoomStore.isOpen && (
			<div className={`zoom ${appStore.theme}`}>
				{mapIsOpen && (
					<div style={{ borderRadius: 6, overflow: 'hidden', marginBottom: 8 }}>
						<ReactOverview
							divClassName="overview-component"
							observedDiagram={graphStore.diagram}
							initOverview={() => {
								return new go.Overview({ contentAlignment: go.Spot.Center })
							}}
						/>
					</div>
				)}
				<div style={{ display: 'flex' }}>
					<Zoom />
					<div
						className="zoom__mapicon"
						onClick={() => setMapIsOpen((e) => !e)}
					>
						<img src={iconMap} alt="/ " />
					</div>
				</div>
			</div>
		)
	)
})

export default Overview
