import { observer } from 'mobx-react-lite'
import { formatDateAndTime } from 'utils/utils'
import styles from './Item.module.scss'

export interface ItemProps {
	item: any
}

export default observer(function Item({ item }: ItemProps) {
	//const { openNotice, toRn } = InfoCardStore;

	return (
		<>
			<tr className={`${styles.item}`}>
				<td className={styles.startSection}>
					<span>{item.productNumber}</span>
				</td>
				<td>
					<span>{item.productName}</span>
				</td>
				<td>
					<span>{item.tnved}</span>
				</td>
				<td>
					<span>
						{item.quantity} {item.unitName}
					</span>
				</td>
				<td className={`${styles.endSection}`}>
					<span>{item.costRub}</span>
				</td>
				<td className={`${styles.startSection}`}>
					<span>{item.sellerId}</span>
				</td>
				<td>
					<span>
						{item.sellerName} {item.sellerLastName} {item.sellerMiddleName}
					</span>
				</td>
				<td className={`${styles.endSection}`}>
					<span>{item.sellerAddress}</span>
				</td>
				<td className={`${styles.startSection}`}>
					<span>{item.buyerId}</span>
				</td>
				<td>
					<span>
						{item.buyerName} {item.buyerLastName} {item.buyerMiddleName}
					</span>
				</td>
				<td className={`${styles.endSection}`}>
					<span>{item.buyerAddress}</span>
				</td>
				<td className={`${styles.startSection}`}>
					<span>{item.docTypeName}</span>
				</td>
				<td>
					<span>{item.docId}</span>
				</td>
				<td>
					<span>{item.docDate ? formatDateAndTime(item.docDate) : '-'}</span>
				</td>
				<td className={`${styles.endSection}`}>
					<span>
						{item.invoiceSer} {item.invoiceNum}
					</span>
				</td>
			</tr>
		</>
	)
})
