import useStores from 'hooks/useStores'
import iconClose from 'icons/icon_close.png'
import { observer } from 'mobx-react-lite'
import List from './components/List'
import styles from './RbCard.module.scss'

export default observer(function RbCard() {
	const { infoCardStore } = useStores()
	const { changeRbDataVisibility, graphIpt } = infoCardStore

	return (
		<div className={styles.card}>
			<div className={styles.card__list}>
				<div className={`body__top ${styles.card__top}`}>
					<h2 className="pagetitle">
						    : {graphIpt}   
					</h2>
					<div className={styles.card__close} onClick={changeRbDataVisibility}>
						<img src={iconClose} alt="" />
					</div>
				</div>
				<List />
			</div>
		</div>
	)
})
