import useStores from 'hooks/useStores'
import { Risk } from 'interface'
import { observer } from 'mobx-react-lite'
import ReactTooltip from 'react-tooltip'
import { getIntegralColor, getNodeStrokeColor } from 'utils/colors'
import {
	getFlagIcon,
	getRiskIcon,
	getTechIconTitle,
	getValueIcon,
	isTech,
} from 'utils/nodes'
import { ReactComponent as ArrowIcon } from './assets/arrow.svg'
import { ReactComponent as Check } from './assets/check.svg'
import styles from './Item.module.scss'

export interface ItemProps {
	item: any
}

export default observer(function Item({ item }: ItemProps) {
	const { graphDetailModalStore } = useStores()
	const { changeSelection, selected } = graphDetailModalStore

	const itemIsSelected = selected.indexOf(item.key) !== -1

	return (
		<tr
			className={`${styles.item} ${itemIsSelected && styles.selected}`}
			onClick={() => changeSelection(item.key)}
		>
			<td>
				<div className={`${styles.check} ${itemIsSelected && styles.selected}`}>
					{itemIsSelected && <Check />}
				</div>
			</td>
			<td>
				<div
					className={styles.integral}
					style={{
						background: `linear-gradient(90deg, ${getIntegralColor(
							item,
						)} 0%, rgba(103, 185, 0, 0) 50%)`,
					}}
				></div>
			</td>
			<td className={styles.inn}>{item.node.inn}</td>
			<td className={styles.shortName}>{item.node.short_name}</td>
			<td className={styles.balance}>
				<span>
					<img src={getValueIcon(item)} alt="" />
					{item.node.balance}
				</span>
			</td>
			<td className={styles.risk}>
				<span>
					{item?.node?.indicators_top?.map((risk: Risk, i: number) => (
						<img
							src={getRiskIcon(risk.Code)}
							alt=""
							key={i}
							data-for={`tooltip_risk_${item.key}_${risk.Code}`}
							data-tip="React-tooltip"
						/>
					))}
				</span>
			</td>
			<td className={styles.inValue}> {item.inValue}</td>
			<td className={styles.outCount}>{item.outCount}</td>
			<td className={styles.outValue}>{item.outValue}</td>
			<td className={styles.tech}>
				{isTech(item) ? (
					<div
						style={{ borderColor: getNodeStrokeColor(item, 'light') }}
						className={styles.tech__icon}
						data-for={`tooltip_tech_${item.key}`}
						data-tip="React-tooltip"
					>
						<img
							src={getTechIconTitle(item?.node?.business_role).icon}
							alt=""
						/>
					</div>
				) : (
					item?.node?.business_role === 'Out_to_EAEU' && (
						<div className={styles.flags}>
							<img
								className={styles.flag}
								src={getFlagIcon(643)}
								alt=""
								data-for={`tooltip_country_1_${item.key}`}
								data-tip="React-tooltip"
							/>
							<ArrowIcon className={styles.arrow} />
							<img
								className={`${styles.flag} ${
									styles['country_' + item?.node?.co_code]
								}`}
								src={getFlagIcon(item?.node?.co_code)}
								alt=""
								data-for={`tooltip_country_2_${item.key}`}
								data-tip="React-tooltip"
							/>
						</div>
					)
				)}
			</td>

			<ReactTooltip
				id={`tooltip_country_1_${item.key}`}
				place="bottom"
				type="light"
			>
				<span className={styles.tooltip}></span>
			</ReactTooltip>
			<ReactTooltip
				id={`tooltip_country_2_${item.key}`}
				place="bottom"
				type="light"
			>
				<span className={styles.tooltip}>{item?.node?.co_code_name}</span>
			</ReactTooltip>
		</tr>
	)
})
