import LiveInput from 'components/shared/LiveInput'
import { CrossIcon, Input, TriangleDownIcon } from 'components/vsa'
import useStores from 'hooks/useStores'
import { observer } from 'mobx-react-lite'
import Select, { components } from 'react-select'
import { getIntegralGradientColor } from 'utils/colors'
import { getRiskIcon } from 'utils/nodes'
import { ReactComponent as FilterIcon } from './assets/filter.svg'
import { ReactComponent as FilterIconActive } from './assets/filterActive.svg'
import Item from './Item'
import styles from './List.module.scss'

export default observer(function List() {
	const { graphDetailModalStore } = useStores()
	const {
		listData,
		selected,
		changeFilter,
		filters,
		innList,
		namesList,
		openedFilter,
		changeOpenedFilter,
		setFilters,
		integralValues,
		riskValues,
	} = graphDetailModalStore

	const _handleKeyDown = (e) => {
		if (e.key === 'Enter') {
			setFilters()
		}
	}

	const onSelectChange = (value, name) => {
		changeFilter(value, name)
		setFilters()
	}

	const integralOptions = integralValues.map((item) => {
		return {
			...item,
			icon: (
				<span
					className={styles.integral}
					style={{
						background: `linear-gradient(90deg, ${getIntegralGradientColor(
							item.value,
						)} 0%, #fff 50%)`,
					}}
				></span>
			),
		}
	})

	const riskOptions = riskValues.map((item) => {
		return {
			...item,
			icon: <img src={getRiskIcon(item.value)} alt={item.label} />,
		}
	})

	const DropdownIndicator = observer((props: any) => {
		return (
			<components.DropdownIndicator {...props}>
				<TriangleDownIcon />
			</components.DropdownIndicator>
		)
	})

	const ClearIndicator = observer((props: any) => {
		return (
			<components.ClearIndicator {...props}>
				<CrossIcon />
			</components.ClearIndicator>
		)
	})

	const Option = observer((props: any) => (
		<components.Option {...props}>
			{props.data.icon}
			<span>{props.data.label}</span>
		</components.Option>
	))

	const SingleValue = observer((props: any) => {
		return (
			<components.SingleValue {...props}>
				{props.data.icon}
				<span>{props.data.label}</span>
			</components.SingleValue>
		)
	})

	return (
		<div className={styles.list}>
			{listData?.length ? (
				<div className={styles.list__table}>
					<table>
						<thead>
							<tr>
								<th></th>
								<th>
									<span className={styles.filters__wrapper}>
										<span
											className={styles.filters__title}
											onClick={() => changeOpenedFilter('integral')}
										>
											
											<span className={styles.filters__icon}>
												{openedFilter === 'integral' ? (
													<FilterIconActive />
												) : (
													<FilterIcon />
												)}
											</span>
										</span>
										{openedFilter === 'integral' && (
											<div
												className={`${styles.filters__container} ${styles.left}`}
											>
												<div className={`${styles.filters__label}`}></div>
												<div className={`${styles.filters__fields}`}>
													<Select
														className={`registry__select ${
															filters.integral && 'with_data'
														}`}
														classNamePrefix={`registry__select`}
														components={{
															DropdownIndicator,
															ClearIndicator,
															Option,
															SingleValue,
														}}
														id="integral"
														isClearable={true}
														isSearchable={false}
														name="integral"
														value={integralOptions.find(
															({ value }) => value === filters.integral,
														)}
														options={integralOptions}
														onChange={(option) =>
															onSelectChange(
																option ? option.value : '',
																'integral',
															)
														}
														placeholder=" "
													/>
												</div>
											</div>
										)}
									</span>
								</th>
								<th>
									<span className={styles.filters__wrapper}>
										<span
											className={styles.filters__title}
											onClick={() => changeOpenedFilter('inn')}
										>
											 
											<span className={styles.filters__icon}>
												{openedFilter === 'inn' ? (
													<FilterIconActive />
												) : (
													<FilterIcon />
												)}
											</span>
										</span>
										{openedFilter === 'inn' && (
											<div
												className={`${styles.filters__container} ${styles.left}`}
											>
												<div className={`${styles.filters__label}`}> </div>
												<div
													className={`${styles.filters__fields} ${styles.fullwidth}`}
												>
													<LiveInput
														name="inn"
														onValueChange={(value) =>
															changeFilter(value, 'inn')
														}
														placeholder=" "
														searchValue={filters.inn}
														searchData={innList}
														onInputKeyDown={_handleKeyDown}
													/>
												</div>
											</div>
										)}
									</span>
								</th>
								<th>
									<span className={styles.filters__wrapper}>
										<span
											className={styles.filters__title}
											onClick={() => changeOpenedFilter('title')}
										>
											 
											<span className={styles.filters__icon}>
												{openedFilter === 'title' ? (
													<FilterIconActive />
												) : (
													<FilterIcon />
												)}
											</span>
										</span>
										{openedFilter === 'title' && (
											<div
												className={`${styles.filters__container} ${styles.left}`}
											>
												<div className={`${styles.filters__label}`}>
													 
												</div>
												<div
													className={`${styles.filters__fields} ${styles.fullwidth}`}
												>
													<LiveInput
														name="title"
														onValueChange={(value) =>
															changeFilter(value, 'title')
														}
														placeholder=" "
														searchValue={filters.title}
														searchData={namesList}
														onInputKeyDown={_handleKeyDown}
													/>
												</div>
											</div>
										)}
									</span>
								</th>
								<th>
									<span className={styles.filters__wrapper}>
										<span
											className={styles.filters__title}
											onClick={() => changeOpenedFilter('balance')}
										>
											
											<span className={styles.filters__icon}>
												{openedFilter === 'balance' ? (
													<FilterIconActive />
												) : (
													<FilterIcon />
												)}
											</span>
										</span>
										{openedFilter === 'balance' && (
											<div className={`${styles.filters__container}`}>
												<div className={`${styles.filters__label}`}>
													 ( - )
												</div>
												<div className={`${styles.filters__fields}`}>
													<Input
														className={`registry__input ${
															filters.balanceFrom && styles.with_data
														}`}
														name="balanceFrom"
														onChange={({ currentTarget }) =>
															changeFilter(currentTarget.value, 'balanceFrom')
														}
														placeholder=""
														value={filters.balanceFrom}
														withReset={true}
														onKeyDown={_handleKeyDown}
													/>{' '}
													-{' '}
													<Input
														className={`registry__input ${
															filters.balanceTo && styles.with_data
														}`}
														name="balanceFrom"
														onChange={({ currentTarget }) =>
															changeFilter(currentTarget.value, 'balanceTo')
														}
														placeholder=""
														value={filters.balanceTo}
														withReset={true}
														onKeyDown={_handleKeyDown}
													/>
												</div>
											</div>
										)}
									</span>
								</th>
								<th>
									<span className={styles.filters__wrapper}>
										<span
											className={styles.filters__title}
											onClick={() => changeOpenedFilter('risk')}
										>
											 
											<span className={styles.filters__icon}>
												{openedFilter === 'risk' ? (
													<FilterIconActive />
												) : (
													<FilterIcon />
												)}
											</span>
										</span>
										{openedFilter === 'risk' && (
											<div className={`${styles.filters__container}`}>
												<div className={`${styles.filters__label}`}>
													 
												</div>
												<div className={`${styles.filters__fields}`}>
													<Select
														className={`registry__select ${
															filters.risk && 'with_data'
														}`}
														classNamePrefix={`registry__select`}
														components={{
															DropdownIndicator,
															ClearIndicator,
															Option,
															SingleValue,
														}}
														id="risk"
														isClearable={true}
														isSearchable={false}
														name="risk"
														value={riskOptions.find(
															({ value }) => value === filters.risk,
														)}
														options={riskOptions}
														onChange={(option) =>
															onSelectChange(option ? option.value : '', 'risk')
														}
														placeholder="  "
													/>
												</div>
											</div>
										)}
									</span>
								</th>
								<th>
									<span className={styles.filters__wrapper}>
										<span
											className={styles.filters__title}
											onClick={() => changeOpenedFilter('inValue')}
										>
											  
											<span className={styles.filters__icon}>
												{openedFilter === 'inValue' ? (
													<FilterIconActive />
												) : (
													<FilterIcon />
												)}
											</span>
										</span>
										{openedFilter === 'inValue' && (
											<div className={`${styles.filters__container}`}>
												<div className={`${styles.filters__label}`}>
													   ( - )
												</div>
												<div className={`${styles.filters__fields}`}>
													<Input
														className={`registry__input ${
															filters.inValueFrom && styles.with_data
														}`}
														name="inValueFrom"
														onChange={({ currentTarget }) =>
															changeFilter(currentTarget.value, 'inValueFrom')
														}
														placeholder=""
														value={filters.inValueFrom}
														withReset={true}
														onKeyDown={_handleKeyDown}
													/>{' '}
													-{' '}
													<Input
														className={`registry__input ${
															filters.inValueTo && styles.with_data
														}`}
														name="inValueTo"
														onChange={({ currentTarget }) =>
															changeFilter(currentTarget.value, 'inValueTo')
														}
														placeholder=""
														value={filters.inValueTo}
														withReset={true}
														onKeyDown={_handleKeyDown}
													/>
												</div>
											</div>
										)}
									</span>
								</th>
								<th>
									<span className={styles.filters__wrapper}>
										<span
											className={styles.filters__title}
											onClick={() => changeOpenedFilter('outCount')}
										>
											  
											<span className={styles.filters__icon}>
												{openedFilter === 'outCount' ? (
													<FilterIconActive />
												) : (
													<FilterIcon />
												)}
											</span>
										</span>
										{openedFilter === 'outCount' && (
											<div className={`${styles.filters__container}`}>
												<div className={`${styles.filters__label}`}>
													   ( - )
												</div>
												<div className={`${styles.filters__fields}`}>
													<Input
														className={`registry__input ${
															filters.outCountFrom && styles.with_data
														}`}
														name="outCountFrom"
														onChange={({ currentTarget }) =>
															changeFilter(currentTarget.value, 'outCountFrom')
														}
														placeholder=""
														value={filters.outCountFrom}
														withReset={true}
														onKeyDown={_handleKeyDown}
													/>{' '}
													-{' '}
													<Input
														className={`registry__input ${
															filters.outCountTo && styles.with_data
														}`}
														name="outCountTo"
														onChange={({ currentTarget }) =>
															changeFilter(currentTarget.value, 'outCountTo')
														}
														placeholder=""
														value={filters.outCountTo}
														withReset={true}
														onKeyDown={_handleKeyDown}
													/>
												</div>
											</div>
										)}
									</span>
								</th>
								<th>
									<span className={styles.filters__wrapper}>
										<span
											className={styles.filters__title}
											onClick={() => changeOpenedFilter('outValue')}
										>
											   (.  )
											<span className={styles.filters__icon}>
												{openedFilter === 'outValue' ? (
													<FilterIconActive />
												) : (
													<FilterIcon />
												)}
											</span>
										</span>
										{openedFilter === 'outValue' && (
											<div className={`${styles.filters__container}`}>
												<div className={`${styles.filters__label}`}>
													   ( - )
												</div>
												<div className={`${styles.filters__fields}`}>
													<Input
														className={`registry__input ${
															filters.outValueFrom && styles.with_data
														}`}
														name="outValueFrom"
														onChange={({ currentTarget }) =>
															changeFilter(currentTarget.value, 'outValueFrom')
														}
														placeholder=""
														value={filters.outValueFrom}
														withReset={true}
														onKeyDown={_handleKeyDown}
													/>{' '}
													-{' '}
													<Input
														className={`registry__input ${
															filters.outValueTo && styles.with_data
														}`}
														name="outValueTo"
														onChange={({ currentTarget }) =>
															changeFilter(currentTarget.value, 'outValueTo')
														}
														placeholder=""
														value={filters.outValueTo}
														withReset={true}
														onKeyDown={_handleKeyDown}
													/>
												</div>
											</div>
										)}
									</span>
								</th>
								<th>
									<span className={styles.filters__wrapper}>
										<span className={styles.filters__title}>
											 
										</span>
									</span>
								</th>
							</tr>
						</thead>
						<tbody>
							{listData.map((item, i) => (
								<Item key={i} item={item} />
							))}
						</tbody>
					</table>
				</div>
			) : (
				<div className={`registry__nodata ${styles.nodata}`}>
					  
				</div>
			)}
			{listData.length > 0 && (
				<div className={`${styles.total}`}>
					: {listData.length}
					{selected.length > 0 && (
						<span className={styles.selectedCount}>
							: {selected.length}
						</span>
					)}
				</div>
			)}
			{/*meta.pagesCount && listData?.length && (
          <div className="registry__pagination small">
            <div className="registry__total">
               : {meta.totalRowsCount}
            </div>
            <Pagination
              onChange={changePage}
              pagination={{
                page: pagination.page,
                count: pagination.count,
                total: pagination.total,
              }}
              showJumpTo={true} />
          </div>
            )*/}
		</div>
	)
})
