import useStores from 'hooks/useStores'
import iconClose from 'icons/icon_close.png'
import iconNegative from 'icons/icon_negative.png'
import { Risk, Vertex } from 'interface'
import { observer } from 'mobx-react-lite'
import { useRef } from 'react'
import { Scrollbars } from 'react-custom-scrollbars-2'
import Draggable from 'react-draggable'
import './PotentialCard.css'

export default observer(function PotentialCard() {
	const { appStore, graphStore } = useStores()
	const nodeRef = useRef<HTMLDivElement>()

	const potentialMode = graphStore.modes.potential

	const PotentialCardItem = observer(
		({ node, risk }: { node: go.Node; risk: any }) => {
			const activeClass =
				potentialMode.currentPotentialRisk?.RiskId === risk.RiskId
					? 'active'
					: ''

			return (
				<div
					className={`potential-card__item ${activeClass}`}
					onClick={() => potentialMode.enable(node, risk)}
				>
					<div className="potential-card__icon">
						<img src={iconNegative} alt="" />
					</div>
					<div className="potential-card__info">
						<div className="potential-card__nodename">{risk.Name}</div>
						<div className="potential-card__riskname">
							{node.data.node.short_name}
						</div>
					</div>
				</div>
			)
		},
	)

	const potentialCardItems = graphStore.graphData.graphNodes?.map(
		(vertex: Vertex, i) => {
			let negativeRisk = null
			if (vertex.node && !vertex.fake && vertex.node.indicators_top) {
				negativeRisk = vertex.node.indicators_top.find(
					(risk: Risk) => risk.Code === 201 && risk.ConnectedNodes.length,
				)
				if (negativeRisk && graphStore.diagram) {
					const node = graphStore.diagram.findNodeForKey(vertex.key)
					return node ? (
						<PotentialCardItem node={node} risk={negativeRisk} key={i} />
					) : null
				}
			}
			return null
		},
	)

	const activeClass = potentialMode.currentPotentialRisk ? 'active' : ''

	return (
		<Draggable nodeRef={nodeRef}>
			<div
				ref={nodeRef}
				className={`potential-card ${
					potentialMode.showCard ? 'open' : 'close'
				} ${appStore.theme} ${activeClass}`}
			>
				<div
					className="potential-card__close"
					onClick={() => potentialMode.disable()}
				>
					<img src={iconClose} alt="" />
				</div>
				<div className="potential-card__title"> </div>
				<div className="potential-card__items">
					<Scrollbars autoHide style={{ height: 270 }}>
						{potentialCardItems}
					</Scrollbars>
				</div>
			</div>
		</Draggable>
	)
})
