import Button from 'components/shared/Button'
import Modal from 'components/shared/Modal'
import useStores from 'hooks/useStores'
import { observer } from 'mobx-react-lite'
import { getIntegralGradientColor } from 'utils/colors'
import { getRiskIcon } from 'utils/nodes'
import { ReactComponent as ClearIcon } from './assets/clear.svg'
import List from './components/List'
import styles from './GraphDetailModal.module.scss'

export default observer(function GraphDetailModal() {
	const { graphDetailModalStore, graphStore } = useStores()
	const {
		modalIsOpen,
		changeModalIsOpen,
		selected,
		groupParent,
		sumInValue,
		activeFilters,
		clearFilters,
		clearFilter,
		integralValues,
		riskValues,
	} = graphDetailModalStore

	let hasActiveFilter = false

	Object.keys(activeFilters).forEach((key) => {
		if (activeFilters[key]) hasActiveFilter = true
	})

	return (
		<Modal
			title=" "
			isOpen={modalIsOpen}
			show={changeModalIsOpen}
		>
			<div className={styles.header}>
				<div className={styles.header__line}>
					<span className={styles.header__label}>:</span>{' '}
					{groupParent?.node?.short_name} (
					<span className={styles.header__label}>:</span>{' '}
					{groupParent?.node?.inn})
				</div>
				<div className={styles.header__line}>
					<span className={styles.header__label}> ( ):</span>{' '}
					<span className={styles.inValue}> {sumInValue}</span>
				</div>
			</div>
			<div className={styles.buttons}>
				<div className={styles.extract}>
					<Button
						color="dark"
						disabled={selected.length === 0}
						onClick={() => graphStore.extractNodes()}
						size="smallest"
					>
						
					</Button>
				</div>
				{hasActiveFilter && (
					<div className={styles.clear}>
						<Button
							color="reset"
							customIcon={<ClearIcon />}
							onClick={clearFilters}
							size="smallest"
						>
							 
						</Button>
					</div>
				)}
				{hasActiveFilter && (
					<div className={styles.filters}>
						{activeFilters.integral && (
							<div className={styles.filter}>
								:{' '}
								<span className={styles.filter__icon}>
									<span
										className={styles.integral}
										style={{
											background: `linear-gradient(90deg, ${getIntegralGradientColor(
												activeFilters.integral,
											)} 0%, #fff 50%)`,
										}}
									></span>
								</span>
								{
									integralValues.find(
										(el) => el.value === activeFilters.integral,
									)?.label
								}
								<span
									className={styles.filter__clear}
									onClick={() => clearFilter(['integral'])}
								>
									<ClearIcon />
								</span>
							</div>
						)}
						{activeFilters.inn && (
							<div className={styles.filter}>
								 : {activeFilters.inn}
								<span
									className={styles.filter__clear}
									onClick={() => clearFilter(['inn'])}
								>
									<ClearIcon />
								</span>
							</div>
						)}
						{activeFilters.title && (
							<div className={styles.filter}>
								 : {activeFilters.title}
								<span
									className={styles.filter__clear}
									onClick={() => clearFilter(['title'])}
								>
									<ClearIcon />
								</span>
							</div>
						)}
						{(activeFilters.balanceFrom || activeFilters.balanceTo) && (
							<div className={styles.filter}>
								:  {activeFilters.balanceFrom || '...'} - {' '}
								{activeFilters.balanceTo || '...'}
								<span
									className={styles.filter__clear}
									onClick={() => clearFilter(['balanceFrom', 'balanceTo'])}
								>
									<ClearIcon />
								</span>
							</div>
						)}
						{activeFilters.risk && (
							<div className={styles.filter}>
								 :{' '}
								<span className={styles.filter__icon}>
									<img
										src={getRiskIcon(activeFilters.risk)}
										alt={activeFilters.risk}
									/>
								</span>
								{
									riskValues.find((el) => el.value === activeFilters.risk)
										?.label
								}
								<span
									className={styles.filter__clear}
									onClick={() => clearFilter(['risk'])}
								>
									<ClearIcon />
								</span>
							</div>
						)}
						{(activeFilters.inValueFrom || activeFilters.inValueTo) && (
							<div className={styles.filter}>
								  : {' '}
								{activeFilters.inValueFrom || '...'} - {' '}
								{activeFilters.inValueTo || '...'}
								<span
									className={styles.filter__clear}
									onClick={() => clearFilter(['inValueFrom', 'inValueTo'])}
								>
									<ClearIcon />
								</span>
							</div>
						)}
						{(activeFilters.outCountFrom || activeFilters.outCountTo) && (
							<div className={styles.filter}>
								  : {' '}
								{activeFilters.outCountFrom || '...'} - {' '}
								{activeFilters.outCountTo || '...'}
								<span
									className={styles.filter__clear}
									onClick={() => clearFilter(['outCountFrom', 'outCountTo'])}
								>
									<ClearIcon />
								</span>
							</div>
						)}
						{(activeFilters.outValueFrom || activeFilters.outValueTo) && (
							<div className={styles.filter}>
								   (.  ): {' '}
								{activeFilters.outValueFrom || '...'} - {' '}
								{activeFilters.outValueTo || '...'}
								<span
									className={styles.filter__clear}
									onClick={() => clearFilter(['outValueFrom', 'outValueTo'])}
								>
									<ClearIcon />
								</span>
							</div>
						)}
					</div>
				)}
			</div>
			<List />
		</Modal>
	)
})
