import { autorun } from 'mobx'
import { useEffect } from 'react'
import GraphStore from 'stores/GraphStore'

export default function useTheme(graphStore: GraphStore) {
	useEffect(() => {
		return autorun(() => {
			const diagram = graphStore.diagram
			if (!diagram) return

			console.debug('[useTheme] autorun')

			graphStore.setGrid()
			graphStore.setWatermark()

			if (graphStore.hasOverlay) {
				graphStore.showOverlay()
			}

			graphStore.setTheme()
		})
	}, [graphStore])
}
