import useStores from 'hooks/useStores'
import iconClose from 'icons/icon_close.png'
import { observer } from 'mobx-react-lite'
import { useRef } from 'react'
import { Option } from 'react-dropdown'
import 'react-dropdown/style.css'
import ReactTooltip from 'react-tooltip'
import { menuIconsDark, menuIconsLight } from 'utils/icons/menuIcons'
import './Menu.css'

export default observer(function Menu() {
	const inputRef = useRef<HTMLInputElement>()
	const { appStore, graphStore, sideMenuStore, zoomStore } = useStores()

	zoomStore.zoomInput = inputRef.current

	const menuIcons = appStore.theme === 'dark' ? menuIconsLight : menuIconsDark
	// const dropdownIcon: any =
	// 	appStore.theme === 'dark' ? dropdownIconLight : dropdownIconDark

	const options: Option[] = [
		{ value: '0', label: ' ' },
		{ value: '1', label: '    ' },
	]

	const defaultOption: Option = options[+graphStore.modes.collapse.isEnabled]

	// const DropdownArrow = () => {
	// 	return <img src={dropdownIcon} alt="" />
	// }

	const changeZoom = () => {
		zoomStore.setZoom(parseInt(inputRef.current.value))
	}

	return (
		<div className={`header ${appStore.theme}`}>
			<div className="header__left">
				<div
					className={`header__item active`}
					onClick={sideMenuStore.changeOpen}
				>
					<img
						src={sideMenuStore.open ? iconClose : menuIcons.iconMenu1}
						alt=""
					/>
				</div>
			</div>
			<div className="header__center">
				<div>{defaultOption.label}</div>
				{/* TA-5769 -        
          <Dropdown
            options={options}
            onChange={(option: Option) => { graphStore.changeMode(option.value) }}
            value={defaultOption}
            placeholder="Select an option"
            arrowClosed={<DropdownArrow />}
            arrowOpen={<DropdownArrow />}
          />*/}
			</div>
			<div className="header__right">
				<div
					className="header__item active"
					onClick={() => appStore.changeTheme()}
				>
					<img
						data-for="React-tooltip13"
						data-tip="React-tooltip"
						src={menuIcons.iconMenu5}
						alt=""
					/>
					<ReactTooltip
						id="React-tooltip13"
						place="bottom"
						type="dark"
						effect="solid"
					>
						\ 
					</ReactTooltip>
				</div>
				<div
					className={`header__item ${zoomStore.isOpen ? 'active' : ''}`}
					onClick={zoomStore.changeZoomVisible}
				>
					<img
						data-for="React-tooltip14"
						data-tip="React-tooltip"
						src={menuIcons.iconMenu6}
						alt=""
					/>
					<ReactTooltip
						id="React-tooltip14"
						place="bottom"
						type="dark"
						effect="solid"
					>
						\ 
					</ReactTooltip>
				</div>
				<div
					className="header__item header__item-zoom"
					onClick={() => inputRef.current.focus()}
				>
					<input
						ref={inputRef}
						data-for="React-tooltip15"
						data-tip="React-tooltip"
						type="number"
						step={10}
						min={zoomStore.minZoom}
						max={zoomStore.maxZoom}
						defaultValue={zoomStore.zoom}
						onBlur={(e) => {
							return zoomStore.setZoom(+e.target.value)
						}}
						onKeyDown={(e) => {
							if (e.key === 'Enter') {
								changeZoom()
								inputRef.current.blur()
							} else if ('-+e.,'.includes(e.key)) {
								e.preventDefault()
							}
						}}
					/>
					<ReactTooltip
						id="React-tooltip15"
						place="bottom"
						type="dark"
						effect="solid"
					>
						
					</ReactTooltip>
				</div>
				<div
					className={`header__item ${appStore.isFullScreen ? 'active' : ''}`}
					onClick={appStore.changeFullScreen}
				>
					<img
						data-for="React-tooltip16"
						data-tip="React-tooltip"
						src={menuIcons.iconMenu8}
						alt=""
					/>
					<ReactTooltip
						id="React-tooltip16"
						place="bottom"
						type="dark"
						effect="solid"
					>
						  
					</ReactTooltip>
				</div>
			</div>
		</div>
	)
})
